
@charset "utf-8";

$download-btn-appstore-width:162px;
$download-btn-appstore-height:60px;
$download-btn-appstore-font-size:16px;

$download-btn-macappstore-width:210px;
$download-btn-macappstore-height:60px;
$download-btn-macappstore-font-size:16px;

$download-btn-googleplay-width:200px;
$download-btn-googleplay-height:60px;
$download-btn-googleplay-background-height:85px;
$download-btn-googleplay-font-size:12px;

/*127,52 168,60*/
$download-btn-microsoftstore-width:168px;
$download-btn-microsoftstore-height:60px;
$download-btn-microsoftstore-background-height:60px;
$download-btn-microsoftstore-font-size:12px;


.mainactions {
  /*max-width: 800px;*/
  max-width: 700px;
  min-width: 250px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-size:0;
}

.download-btn {
  color:#FFFFFF;
  font-weight:bold;
  /*text-transform:uppercase;*/
  text-decoration:none;
  border-radius:6px;
  transition:0.1s ease;
  display: inline-flex;
  margin: 0.4em; 
  background-color: red;
}

.download-btn-gongyixing {
    width: $download-btn-appstore-width;
    height: $download-btn-appstore-height;
    line-height: $download-btn-appstore-height;
    font-size:$download-btn-appstore-font-size;
    background: url(/assets/images/badges/Download_on_the_Gongyixing.svg) no-repeat center;
    background-size: $download-btn-appstore-width $download-btn-appstore-height;
    /*text-transform:uppercase;*/
    font-variant: all-petite-caps;
}

.download-btn-gongyixing span {
    width: $download-btn-appstore-width;
    text-align: center;
    font-size: 16px;
    color: #fff;
}

.en .download-btn-gongyixing span{
  font-size: 18px;
}
.zh .download-btn-gongyixing span{
  font-size: 28px;
}
.tc .download-btn-gongyixing span{
  font-size: 28px;
}

@media screen {

  .download-btn span {
    visibility: hidden;
  }

  .download-btn-gongyixing span {
    visibility: visible;
  }

}

